import {Directive, ElementRef, HostListener} from '@angular/core';
import {NgModel} from "@angular/forms";
import {PhonePipe} from "../filters/phone.pipe";

@Directive({
  selector: '[ngModel][apPhone]',
  providers: [NgModel]
})
export class PhoneDirective {

  elem: HTMLInputElement;

  constructor(private el: ElementRef, private phonePipe:PhonePipe,private ngModel: NgModel) {
    if(!this.ngModel.options){
      this.ngModel.options = {};
    }
    this.elem = el.nativeElement
  }

  @HostListener("blur",["$event.target.value"])
  onBlur(value) {
    if(value == ''){
      this.ngModel.reset();
      //this.elem.value= '';
    }else if(this.el.nativeElement.id == "contactPhone"){
      this.elem.value = this.formatPhoneNumber(value);
    }
  }

  @HostListener("focus",["$event.target.value"])
  onFocus() {
    if(this.el.nativeElement.id == "contactPhone"){
      this.ngModel.reset();
    }
  }

  formatPhoneNumber(input:string){
    if(input.length === 10){
      return input.substring(0, 3)+'-'+input.substring(3, 6)+'-'+input.substring(6, 10);
    }
  }

}
