import { Component, OnInit } from '@angular/core';
import {CookieService} from "ngx-cookie-service";
import {Router} from "@angular/router";
import {HttpClient} from "@angular/common/http";
import {CustomerService} from "../../services/customer.service";

@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.css']
})
export class ContactUsComponent implements OnInit {

  emailPattern = '([a-zA-Z0-9]+)([\\_\\.\\-{1}])?([a-zA-Z0-9]+)\\@([a-zA-Z0-9]+)([\\.])([a-zA-Z\\.]+)';
  namePattern = '^[a-zA-ZùûüÿàâæçéèêëïîôÙÛÜÀÂÆÇÉÈÊËÏÎÔ \'-]+$';
  phonePattern = '[0-9]{3}-[0-9]{3}-[0-9]{4}';
  contactInfo = {
    email: '',
    name: '',
    message: '',
    phoneNumber: ''
  };

  customerData:any;

  data_:any;
  dataOriginal:any;

  constructor(private _http: HttpClient, private router: Router, private cookieService: CookieService,private customerService : CustomerService ) { }

  ngOnInit() {
  }

  disableEnterKey(event) {
    if (event.keyCode === 13) {
      event.preventDefault();
      return false;
    }
  }

  bookService(){
    this.data_ = {
      'name': this.contactInfo.name,
      'email':this.contactInfo['email'].substring(0,this.contactInfo['email'].indexOf('@'))+'*******',
      'message': this.contactInfo.message,
      'phone': this.contactInfo.phoneNumber
    };
    this.dataOriginal = {
      'name': this.contactInfo.name,
      'email':this.contactInfo.email,
      'phone': this.contactInfo.phoneNumber,
      'message': this.contactInfo.message
    };
    this.customerData = JSON.stringify(this.data_);

    this.customerService.saveClient(this.dataOriginal).subscribe((data)=>{
      console.log('saving data:'+ data);
    });

   this.customerService.sendClientData(this.data_).subscribe((data:any)=>{
      console.log(this.data_);
      if(data){
        console.log("successfully sent email");
        this.router.navigate(['success']);
      }else{
        console.log("opps something went wrong");
      }
    });

  }

}
